.Jumbotron {
  position: relative;
  display: flex;
  width: 100%;
  height: 400px;
}
.JumbotronBackground {
  display: flex;
  justify-content: center;
  background-image: url('../../../images/배경0.jpg');
  background-size: cover;
  width: 100%;
  height: 100%;
}
.JumbotronHello {
  position: absolute;
  font-size: 72px;
  text-shadow: 0px 0px 10px;
  top: 20%;
  left: 50%;
  transform: translate(-50%, 0%);
  color: white;
}
.JumbotronWelcome {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  color: white;
  font-size: 20px;
  text-shadow: 0px 0px 10px;
}
.JumbotronButton {
  position: absolute;
  width: 130px;
  height: 50px;

  border-radius: 35px;
  background: #282828;
  top: 70%;
  left: 50%;
  transform: translate(-50%, 0%);
  color: white;
  background-color: rgb(100, 100, 100);
}
